.wp-block-masterng-masters {
	border: 1px dotted #f00;
}
.btn-appointment {
    filter: drop-shadow(0px 6px 6px rgba(0, 0, 0, 0.25));
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: black;
    background: #EBEBEB;
    border: 0;
    border-radius: 25px;
    padding-top: 9px;
    padding-bottom: 10px;
    padding-left: 32px;
    padding-right: 32px;
    transition: all 0.5s ease 0s;
    width: 200px;
    height: 40px;
}

